.project-card {
  color: navy;
  width: 250px;
  height:400px;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  margin: 0 10px;
  transition: transform 0.3s ease-in-out;
  border-radius: 8px;
  background-color: #fff;
align-self: center;
}

.projectsSlider {
  width: 100%;
  
  /* Add any other styles you need for your .projectsSlider */
}

.projects {
  color: navy;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center; /* Align center on both axes */
  text-align: center;
 
  /* Add more styles here if needed */
}

.project-card:hover {
  transform: scale(1.05);
}

.project-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
}

.project-card h2 {
  margin: 10px 0;
  font-size: 1.5rem;
}

.project-card p {
  color: navy;
}

.project-card button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

.project-card button:hover {
  background-color: #0056b3;
}

/* Media Query for mobile devices */
@media screen and (max-width: 767px) {
  .projects {
    width: 100%;
    align-items: center; /* Center the items on mobile */
    text-align: center; /* Center the text on mobile */
  }

  .project-card {
    width: 70%;
    height:370px; /* Make the cards full width on mobile */
  }
}
.custom-arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  font-size: 24px; /* Adjust the font size as needed */
  color: #ffffff;
  z-index: 2000; /* Adjust the color as needed */
}

.custom-prev-arrow {
  left: 10px;
}

.custom-next-arrow {
  right: 10px;
}
.slick-dots {
  position: static;
  background-color: #ffffff00;
}
.arrow-box {
  width: 40px; /* Adjust the width as needed */
  height: 40px; /* Adjust the height as needed */
  background-color: #007bff; /* Adjust the background color as needed */
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px; /* Adjust the border-radius as needed */
  transition: background-color 0.3s ease-in-out;
  z-index: 2000;
}

.arrow-box:hover {
  background-color: #0056b3; /* Adjust the hover background color as needed */
}