/* Navigation.css */
.navigation {
  position: absolute;
  top: 0;
  left: 0;
  width: 70%;
  
  background-color: rgba(255, 255, 255, 0.185);
  padding: 1rem;
  display: flex;
  justify-content: flex-start; /* Align items to the left */
 
  z-index: 1000;
  gap: 10px;
}

.menu-toggle {
  display: none;
}

.logo-container {
  display: flex;
  align-items: center;
}

.logo {
  width: 30px;
  height: auto;
  margin-right: 0.5rem;
}

.logo-text {
  color: white;
  font-weight: bold;
  font-size: 1.2rem;
}



ul {
  list-style-type: none;
  display: flex;
  gap: 1rem;
}

li {
  cursor: pointer;
  color: white;
  font-weight: bold;
}

li:hover {
  text-decoration: underline;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .logo {
    display: none;
  }

  .navigation {
    width: 80%;
    gap:70%;
  }

  .logo-text {
    font-size: 1.5rem;
  }
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  .menu-toggle {
    font-size: 1.5rem;
    display: block;
    color: rgb(255, 255, 255);
    background: none;
    border: none;
    cursor: pointer;
  
  }

  ul {
    flex-direction: column;
    position: absolute;
    top: 64px;
    left: 0;
    width: 50%;
    background-color: rgb(255, 255, 255);
   
    padding: 0.5rem;
    display: none;
    border-radius: 20px;
    animation: fadeIn 0.3s ease-in-out forwards;
    opacity: 0;
  }

  .navigation:hover ul,
  .menu-toggle:focus + ul {
    display: flex;
  }

  .nav-links.show {
    transform: translateX(0);
  }

  li {
    margin-bottom: 0.5rem;
    color:rgb(0, 191, 255);
  }
}
