body {
  background-color: rgba(187, 233, 247, 0.71);
  overflow-x: hidden;
  gap: 200px;
}

.hamster-image {
  display: flex;
  position: absolute;
  left: 100px;
  top: 80%;
  width: 600px;
  height: 600px;
  z-index: 1;
 
}
#about {
  display: flex;
  position: absolute;
  top: 240%;
  width: 50%;
  height: 30%;
  align-self: center;
  float: right;
  left: 45%;
}
.logoanim {
  display: flex;
  position: absolute;
  left: 386px;
  top: 240%;
  width: 400px;
  height: 400px;
  z-index: 1;

}
#projects {
  display: flex;
  position: absolute;
  top: 80%;
  width: 50%;
  height: 30%;
  align-self: center;
  float: right;
  left: 45%;
}
#footer {
  display: flex;
  position: absolute;
  top: 310%;
  width: 100%;
  height: 100px;
  align-self: center;
 
 
}
.projects-container {
  overflow: hidden; /* Clearing float */
}

.hello-container,
.tech-stack-container,
.about-me-container {
  text-align: center;
  margin-bottom: 2rem; /* Dodano margines między sekcjami */
}

.hello-title,
.tech-stack-title,
.about-title {
  font-size: 2rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.about-subtitle {
  font-weight: bold;
}

.heroanim {
  z-index: 1;

  position: absolute;
  right: 22%;
  top: 25%;
  width: 250px;
  height: 500px;
  /* Dodaj dodatkowe style w razie potrzeby */

  /* Stylizacja dla ekranów o mniejszej szerokości */
  @media screen and (max-width: 768px) {
    transform: scale(1);
    position: absolute;
    right: 1%;
    top: 55%;
    width: 100px;
    height: 200px;
  }
}

@media screen and (max-width: 768px) {
  #projects {
    display: flex;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    height: 50%;
    align-self: center;
    border-radius: 15px;
  }

  .hamster-image {
    display: none;
  }
  #about {
    display: flex;
    position: absolute;
    top: 250%;
    left: 0;
    width: 100%;
    height: 50%;
    align-self: center;
    border-radius: 15px;
  }
  .logoanim {
  display: none;
  }
}
